<template>
  <div class="check-form">
    <div class="my-tabs" id="doc-my-tabs">
        <div class="my-tabs-bd">
          <div v-if="!(faguiList.length>0)" style="text-align: center;line-height: 100px;font-size: 30px;"><i class="el-icon-loading"></i> 加载中......</div>
          <el-row v-else>
            <el-col :span="12">
              <el-collapse v-if="faguiList.length>0" v-model="activeName" accordion>
              <el-collapse-item v-for="(listItem,index) in faguiList" :name="listItem.icon">
                <template slot="title">
                  <div class="title" :title="listItem.icon">{{listItem.icon}}</div>
                </template>
                <!-- <div style="position: absolute;top:0;right: 0px;"></div> -->
                <div class="content">
                  <div class="btn_group"><span @click="tab=1" class="btn_xq" :class="{'active':tab==1}">文章详情</span> <span @click="clickTab()" class="btn_fc" :class="{'active':tab==2}">文章分词</span><span v-if="tab==2" style="float: right;" @click="allCheck()" class="btn_allCheck">全部作为标签</span></div>
                  <div v-if="faguiXq=='null'" style="margin-top:10px;text-align: center;"><i class="el-icon-loading"></i> 正在加载内容</div>
                  <div v-else-if="tab==1">
                    <p class="item-b">{{listItem.desc}} &nbsp;&nbsp;|&nbsp;&nbsp; {{listItem.time}}</p>
                    <div v-html="faguiXq"></div>
                  </div>
                  <div v-else-if="tab==2">
                    <ul v-if="faguiXqText" class="faguiXqBox">
                      <li v-for="item in faguiXqText" class="item dragitem">
                        {{item.name}}
                      </li>
                    </ul>
                     <div v-else style="margin-top:10px;text-align: center;"><i class="el-icon-loading"></i> 正在加载内容</div>
                  </div>
                </div>
              </el-collapse-item>
            </el-collapse>
                  <div class="block">
                <el-pagination
                  @current-change="handleCurrentChange()"
                  :current-page.sync="currentPage"
                  :page-size="10"
                  background
                  layout="prev, pager, next, jumper"
                  :total="10*pages">
                </el-pagination>
            </div>
            </el-col>
            <el-col :span="12" class="words">
              <div class="inputs">
                <ul v-if="faguiList.length>0">
                  <li class="input-item" v-for="(listItem,index) in faguiList">
                    <textarea :id="'box'+index" class="wordInput clear1" type="text"  :name="listItem.icon" placeholder="标签...(多个标签用,隔开)">
                    </textarea>
                  </li>
                  <li>
                    <div class="btn-box">
                      <button type="button" class="clearBtn am-btn am-btn-default" @click="clear(1)">清空</button>
                    </div>
                  </li>
                  <li class="input-item other">
                    <label>添加目标词汇：</label><br/>
                    <textarea id="mbch" style="height: 120px;" class="wordInput clear2" type="text" placeholder="添加目标词汇(多个词汇用,隔开)">
                    </textarea>
                  </li>
                  <li class="input-item other">
                    <label>添加停用词汇：</label><br/>
                    <textarea id="tych" style="height: 120px;" class="wordInput clear2" type="text" placeholder="添加停用词汇(多个词汇用,隔开)">
                    </textarea>
                  </li>
                  <li>
                    <div class="btn-box">
                      <button type="button" class="submitBtn am-btn am-btn-primary" @click="submit()">提交</button>
                      <button type="button" class="clearBtn am-btn am-btn-default" @click="clear(2)">清空</button>
                    </div>
                  </li>
                </ul>
              </div>
              <ul class="wordBox">
              </ul>
            </el-col>
          </el-row>
          <div class="am-modal am-modal-confirm" tabindex="-1" id="my-confirm">
            <div class="am-modal-dialog" style="width:1000px;">
              <div class="am-modal-hd" style="color: #b80000;border-bottom: 1px solid #dedede;margin-bottom: 10px;line-height: 50px;">核对提交信息</div>
              <div class="am-modal-bd" style="height:600px;overflow-y:auto">
                <ul class="msgBox" style="width:60%;margin:0 auto;">
                  
                </ul>
              </div>
              <div class="am-modal-footer">
                <span class="am-modal-btn" data-am-modal-cancel>取消</span>
                <span class="am-modal-btn" data-am-modal-confirm>确定</span>
              </div>
            </div>
          </div>
        </div>
    </div>
</div>
</template>
<script type="text/javascript">
  import reqData from "./../../../reqData.js"
  import amazeui from 'amazeui';
  require('amazeui/dist/css/amazeui.css');
  require('./../image/commen.css');
  import './../image/jquery.easyui.min.js'
  export default{
    name:'biaoshi',
    data(){
      return {
        currentPage: 1,
        pages:0,
        faguiList:[],
        faguiXq:{},
        faguiXqText:[],
        activeName: '1',
        tab:1
      }
    },
    components:{
    },
    methods:{
        getListPages(){
          reqData
              .req({
                apiName: "listPages",
                data: {             
                  name:',',
                  company_name: '招商银行股份有限公司'
                }
              })
              .then(res => {
                res = res.data;
                if (res && res.code === "200") {
                  let data = res.data;
                  console.log(data);
                  this.pages=Number(data);
                }
              })
              .catch(err => {
                console.log(err);
             });
        },
        handleCurrentChange(val){
          $('.inputs .input-item .wordInput').val('');
          reqData
              .req({
                apiName: "faguiList2",
                data: {             
                  num:this.currentPage,
                  name:',',
                  company_name: '招商银行股份有限公司'
                }
              })
              .then(res => {
                res = res.data;
                if (res && res.code === "200") {
                  let data = res.data;
                  console.log(data);
                  this.faguiList=data;
                }
              })
              .catch(err => {
                console.log(err);
             });
        },
        getFaguiXq(title){
          let _this=this;
          this.faguiXq='null';
          this.tab=1;
          reqData
              .req({
                apiName: "faguiXq",
                data: {             
                  ID:1,
                  icon_name:title
                }
              })
              .then(res => {
                res = res.data;
                if (res && res.code === "200") {
                  let data = res.data;
                  console.log(data);
                  let cont=data[0].text;
                  let reg1=new RegExp("。","g");
                  let reg3=new RegExp(" ","g");
                  cont=cont.replace(reg1,"。</p><p>");
                  cont=cont.replace(reg3,"</p><p>");
                  cont="<p>"+cont+"</p>";
                  this.faguiXq=cont;

                  let filesText=data[0].icon+" "+data[0].desc+" "+data[0].text;
                  // 给文章做分词
                  reqData
                    .req({
                      apiName: "fenci",
                      data: {
                        "text": filesText,
                      },
                    })
                    .then(res => {
                      res = res.data;
                      if (res && res.code === "200") {
                        $('.faguiXqBox').empty();
                        let data = res.data;
                        console.log(data);
                        this.faguiXqText=data;
                        // data.forEach((value)=>{
                        //   $('.faguiXqBox').append('<li class="item dragitem" style="float: left;cursor: pointer;margin: 3px 5px;border-radius: 4px;padding: 2px 4px;box-sizing: border-box;background-color: #c8deff;color: #000;font-size: 15px;">'+value.name+'</li>');
                        // });
                        // _this.drag();
                        setTimeout(function(){ _this.drag(); }, 1000);
                      }
                    })
                    .catch(err => {
                      console.log(err);
                   });

                }
              })
              .catch(err => {
                console.log(err);
             });
        },
        clickTab(){
          this.tab=2;
          let _this=this;
          setTimeout(function(){ _this.drag(); }, 400);
        },
        //        拖拽事件
        drag(){
          $('.dragitem').draggable({
              revert:true,
              proxy:'clone',
              onStartDrag:function(){
                // console.log("aaaaaaaaaa");
                  $(this).draggable('options').cursor = 'not-allowed';
                  $(this).draggable('proxy').css('z-index',999);
              },
              onStopDrag:function(){
                  $(this).draggable('options').cursor='move';
              }
          });
          $('.wordInput').droppable({
              onDragEnter:function(e,source){
                  $(source).draggable('options').cursor='auto';
              },
              onDragLeave:function(e,source){
                  $(source).draggable('options').cursor='not-allowed';
              },
              onDrop:function(e,source){
                var id=$(this)[0].id;
                if(id=='tych'){
                  $(source).addClass('clear');
                  $(source).css('display','none');
                }
                var name = $.trim($(source).html());
                console.log(name+'===='+id);
                addItem(name,id);
              }
          });
          function addItem(name,id) {
              var text=$('#'+id).val();
              $('#'+id).val(text+name+',')
          }
          // $('.btn-box .submitBtn').click(function(){
            //   console.log('aa');
            //   var inputs=$('.inputs .input-item');
            //   var datas=[];
            //   for(var i=0;i<inputs.length;i++){
            //     var icon=$('.wordInput',inputs[i])[0].name;
            //     var cihui=$('.wordInput',inputs[i]).val()?$('.wordInput',inputs[i]).val():',';
            //     var reg=new RegExp("，","g");
            //     cihui=cihui.replace(reg,",");
            //     if(cihui.search(",")<0){
            //             cihui=cihui+","
            //           };
            //     datas.push({
            //       icon:icon,
            //       cihui:cihui
            //     })
            //   };
            //   console.log(datas);
            // reqData
            //     .req({
            //       apiName: "biaozhuSubmit",
            //       data:{
            //         data:JSON.stringify(datas)
            //       }
            //     })
            //     .then(res => {
            //       res = res.data;
            //       console.log(res.message);
            //       if(res.message==1){
            //         alert("提交成功");
            //         // $('.inputs .input-item .wordInput').val('');
            //       }else{
            //         alert("提交失败")
            //       }
            //     })
            //     .catch(err => {
            //       console.log(err);
            //    });
          // });
        },
        clear(i){
          $('.inputs .input-item .clear'+i).val('');
        },
        submit(){
          let _this=this;
          $('.msgBox').empty();
          var inputs=$('.inputs .input-item');
          var datas=[];
          var ch_datas=[];

          for(var i=0;i<inputs.length;i++){
            var icon=$('.wordInput',inputs[i])[0].name;
            var cihui=$('.wordInput',inputs[i]).val()?$('.wordInput',inputs[i]).val():',';
            var reg=new RegExp("，","g");
            cihui=cihui.replace(reg,",");
            if(cihui.search(",")<0){
                    cihui=cihui+","
                  };
            if(icon){
              datas.push({
                icon:icon,
                cihui:cihui
              })
            }else{
              ch_datas.push({
                cihui:cihui
              })
            }
          };
          for (var i = 0; i < datas.length; i++) {
            $('.msgBox').append('<li>'+
                '<label>'+datas[i].icon+'</label><br/>'+
                '<textarea type="text" name="'+datas[i].icon+'" placeholder="标签...(多个标签用,隔开)" style="width:100%;height:100px;overflow-y:auto">'+
                datas[i].cihui+
                '</textarea>'+
              '</li>')
          }
          for (var i = 0; i < ch_datas.length; i++) {
            $('.msgBox').append('<li>'+
                '<label>'+(i==0?'目标词汇':'停用词汇')+'</label><br/>'+
                '<textarea type="text" placeholder="标签...(多个标签用,隔开)" style="width:100%;height:100px;overflow-y:auto">'+
                ch_datas[i].cihui+
                '</textarea>'+
              '</li>')
          }
          $('#my-confirm').modal({
            relatedTarget: this,
            onConfirm: function(options) {
              var datas_1=[];
              var ch_datas_1=[];
              var msgBox=$('.msgBox li');
              for(var i=0;i<msgBox.length;i++){
                var icon=$('textarea',msgBox[i])[0].name;
                var cihui=$('textarea',msgBox[i]).val()?$('textarea',msgBox[i]).val():',';
                // var cihui=$('textarea',msgBox[i]).val();
                var reg=new RegExp("，","g");
                cihui=cihui.replace(reg,",");
                if(cihui.search(",")<0){
                  cihui=cihui+","
                };
                if(icon){
                  datas_1.push({
                    icon:icon,
                    cihui:cihui
                  })
                }else{
                  ch_datas_1.push({
                    cihui:cihui
                  })
                }
              }
              console.log(datas_1);
              console.log(ch_datas_1);
              reqData
                .req({
                  apiName: "biaozhuSubmit",
                  data:{
                    data:JSON.stringify(datas_1)
                  }
                })
                .then(res => {
                  res = res.data;
                  console.log(res.message);
                  if(res.message==1){
                    $.ajax({
                        url: "http://121.42.29.188:10000/zhuajiacihui_cihuicunchu",
                        type: 'post',
                        dataType: "json",
                        data: {
                          "username":'aaa',
                          "qiye_id":'cihui',
                          "feilei_id":'cihui',
                          "yidian_name":'cihui_yaosu',
                          "word1": ch_datas_1[0].cihui,
                          "word2": ch_datas_1[1].cihui
                        },
                        success: function (result) {
                            console.log(result);
                            alert('提交成功');
                        },
                        error:function(){
                          alert("提交失败");
                        }
                    })
                    // $('.inputs .input-item .wordInput').val('');
                  }else{
                    alert("提交失败")
                  }
                })
                .catch(err => {
                  console.log(err);
               });
            },
            // closeOnConfirm: false,
            onCancel: function() {
              
            }
          });
        },
        allCheck(){
          let biaoqian=$('.el-collapse-item.is-active .faguiXqBox>li:not(.clear)').text();
          console.log(biaoqian);
          biaoqian = biaoqian.replace(/\s+/g,",");
          let index=$('.el-collapse-item.is-active').index()+1
          console.log(biaoqian);
          console.log($('.el-collapse-item.is-active').index());  
          $('.biaozhu .fc-box .words .inputs .input-item:nth-child('+index+') textarea').val($('.biaozhu .fc-box .words .inputs textarea').val()+biaoqian)
        }
    },
    computed:{
      userName(){
        return sessionStorage.getItem('username');
      }
    },
    watch:{
      activeName: function (val){
        if(val){
          this.getFaguiXq(val)
        }
      }
    },
    mounted: function(){
        this.getListPages();
        this.handleCurrentChange(1);
        reqData
          .req({
            apiName: "fgBiaozhu",
          })
          .then(res => {
            res = res.data;
            if (res && res.code === "200") {
              let data = res.data;
              console.log(data);
              data.forEach((value)=>{
                $('.wordBox').append('<li class="item dragitem">'+value+'</li>');
              });
              this.drag();
            }
          })
          .catch(err => {
            console.log(err);
          });
    }
  }
</script>
<style>
  /*此行类别CSS*/
  .biaozhu .yc-box {
      width: 100%;
      height: 140px;
      /*line-height: 85px;*/
      background-color: #fff;
      box-shadow: 0px 5px 6px 2px rgba( 0, 0, 0 ,0.1);
  }
  .biaozhu .banner{
      width: 100%;
      height: 140px;
      /*line-height: 140px;*/
      text-align: center;
  }
  .biaozhu .banner .img-title{
      display: inline-block;
      width: 394px;
      height: 85px;
      margin-left: 20%;
      background: url(../image/title3.png) no-repeat;
      background-position: 0 90%;
      margin-top: 28px;
  }
  .biaozhu .am-container{
      max-width: 1500px;
      box-sizing: border-box;
  }
  .biaozhu .fc-box{
    overflow: hidden;
    /*width: 80%;
    margin: 0 auto;*/
    min-height: 500px;
    padding:2%;
    border: 1px solid #ccc;
    margin-bottom: 20px;
    box-sizing: border-box;
  }
  .biaozhu .check-form .my-tabs{
      width: 100%;
      min-height: 700px;
      padding: 30px 60px;
      box-sizing: border-box;
      text-align: justify;
      /*border: 2px solid #0fb3dc;*/
      border-radius: 8px;
      background-color: #fff;
       box-shadow: 0 1px 6px rgba(0,0,0,0.4);
  }

  .biaozhu .fc-box .words{
    position: relative;
  }
  .biaozhu .fc-box .words .inputs{
    position: absolute;
    top: 0;
    left: 20px;
    width: 220px;
    z-index: 990;
    max-height: 400px;
    overflow-y: auto;
  }
  .biaozhu .fc-box .words .inputs .btn-box{
    text-align: center;
  }
  .biaozhu .fc-box .words .inputs .btn-box .submitBtn,
  .biaozhu .fc-box .words .inputs .btn-box .clearBtn {
    display: inline-block;
    margin-bottom: 0;
    width: 40%;
    padding: 0; 
    vertical-align: middle;
    font-size: 16px;
    font-weight: normal;
    line-height: 30px;
    border-radius: 6px;
    text-align: center;
    white-space: nowrap;
    background-image: none;
    border: 1px solid transparent;
  }
    /* border-radius: 0; */
   /* .biaozhu .fc-box .words .inputs .btn-box .submit,
    .biaozhu .fc-box .words .inputs .btn-box .clear{
      display: inline-block;
      width: 40%;
      height: 44px;
      background-color: 
      margin-top: 15px 2%;
      cursor: pointer;
    }*/
  .biaozhu .fc-box .words .inputs li{
    height: 50px;
    line-height: 46px;
    margin-bottom: 6px;
    clear: both;
  }
  .biaozhu .fc-box .words .inputs li.other{
    height: 160px;
    line-height: 32px;
  }
  .biaozhu .fc-box .words .inputs textarea{
    position: absolute;
    height: 50px;
    line-height: 20px;
    overflow-y: auto;
    width: 200px;
    padding: 5px 10px;
    box-sizing: border-box;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  .biaozhu .fc-box .wordBox{
    position: relative;
    max-height: 600px;
    overflow-y: auto;
    padding-left: 250px;
  
  }
  .content .btn_group{
    margin-bottom: 20px;
  }
  .content .btn_group>span{
    background-color: #ccc;
    padding: 4px 8px;
    box-sizing: border-box;
    border-radius: 4px;
    cursor: pointer;
  }
  .content .btn_group>span.btn_allCheck{
    background-color: #e6d2b8;
  }
  .content .btn_group>span.active{
    background-color: #b80000;
    color: #fff;
  }
  .biaozhu .fc-box .wordBox>li.item,
  .biaozhu .check-form .my-tabs .faguiXqBox>li.item{
    float: left;
    cursor: pointer;
      margin: 3px 5px;
      border-radius: 4px;
      padding: 2px 4px;
      box-sizing: border-box;
      background-color: #c8deff;
      color: #000;
      font-size: 15px;
  }
/*//折叠面板样式*/
  .biaozhu .el-collapse-item__wrap {
      overflow: auto;
      height: 300px;
      background-color: #fff;
      padding: 20px;
  }
  .biaozhu .el-collapse-item__content{
    font-size: 14px;
  }
  .biaozhu .el-collapse-item__header {
    position: relative;
    color: #303133;
    padding-left: 20px;
    border-bottom: none;
    margin-bottom: 6px;
    font-size: 16px;
    font-weight: 500;
    height: 50px;
    line-height: 50px;
    background-color: rgba(227,233,242,1);
    transition: border-bottom-color .3s;
    outline: 0;
  }
  .biaozhu .el-collapse-item__header .title{
    white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
  }
  .biaozhu .el-collapse-item:nth-child(even) .el-collapse-item__header{
    background-color: rgba(227,233,242,0.7);
  }
  .biaozhu .block{
    text-align: center;
  }

  /*修改分页样式*/
  .biaozhu .el-pagination.is-background .el-pager li:not(.disabled).active {
      background-color: #409eff;
      color: #fff;
  }
  .biaozhu .el-pagination.is-background .el-pager li:not(.disabled).active:hover{
    background-color: #409eff;
      color: #fff;
  }
  .biaozhu .el-pagination.is-background .el-pager li:not(.disabled):hover {
      color: #409eff;
  }
  .biaozhu .el-pagination.is-background .btn-next:disabled,
  .biaozhu .el-pagination.is-background .btn-prev:disabled{
    color: #409eff;
  }
  .biaozhu .el-input__inner:hover, .el-input__inner:focus {
      border: 1px solid #409eff;
  }
</style>


